// src/context/AppDataContext.js
import React, { createContext, useState, useContext, useEffect, useCallback } from 'react';

// URL do API Google Sheets
const API_URL = 'https://script.google.com/macros/s/AKfycbwnP4fnHSOtmyyyNeeDWfpifL92F1w3hAV-D2WY7AhqdjHoz29RD86pe_KWnR35LFqdfw/exec';

// Utworzenie kontekstu
const AppDataContext = createContext();

// Hook do uywania kontekstu
export const useAppData = () => useContext(AppDataContext);

// Komponent Provider dla kontekstu
export const AppDataProvider = ({ children }) => {
  const [appData, setAppData] = useState({
    general: {},
    departments: [],
    functions: [],
    users: [],
    timeTrends: {
      quarters: [],
      weeks: [],
      days: []
    },
    meta: {
      lastUpdate: null,
      availableFunctions: []
    }
  });

  const [filters, setFilters] = useState({
    period: 'all',
    app: 'all',
    function: 'all'
  });

  const [dataLoaded, setDataLoaded] = useState(false);
  
  // Stan rda danych
  const [dataSource, setDataSource] = useState('file'); // 'file' lub 'api'
  
  // Stan interwau odwieania (w minutach)
  const [refreshInterval, setRefreshInterval] = useState(5 * 60 * 1000); // domylnie 5 minut
  
  // Stan automatycznego odwieania
  const [autoRefresh, setAutoRefresh] = useState(false);

  // Aktualizacja danych aplikacji
  const updateAppData = (newData, source = 'file') => {
    setAppData(newData);
    setDataLoaded(true);
    setDataSource(source);
    
    // Jeli dane pochodz z API, wczamy automatyczne odwieanie
    if (source === 'api') {
      setAutoRefresh(true);
    }
  };

  // Aktualizacja filtrw
  const updateFilters = (newFilters) => {
    setFilters({ ...filters, ...newFilters });
  };
  
  // Zmiana interwau odwieania
  const changeRefreshInterval = (intervalInMinutes) => {
    setRefreshInterval(intervalInMinutes * 60 * 1000);
  };
  
  // Wczanie/wyczanie automatycznego odwieania
  const toggleAutoRefresh = () => {
    setAutoRefresh(!autoRefresh);
  };
  
  // Rczne odwieenie danych z API - uyj useCallback aby unikn rekonstrukcji
  const refreshData = useCallback(async () => {
    // Odwieamy tylko jeli dane pochodz z API
    if (dataSource === 'api' && dataLoaded) {
      try {
        console.log('Rozpoczynam odwieanie danych z API:', API_URL);
        const response = await fetch(API_URL);
        console.log('Status odpowiedzi:', response.status, response.statusText);
        
        if (!response.ok) {
          const errorText = await response.text();
          console.error('Tre odpowiedzi z bdem:', errorText);
          throw new Error(`Problem z pobraniem danych: ${response.statusText}. Status: ${response.status}`);
        }
        
        // Sprawd, czy odpowied jest w formacie JSON
        let data;
        const responseText = await response.text();
        console.log('Tre odpowiedzi (pierwsze 200 znakw):', responseText.substring(0, 200));
        
        try {
          data = JSON.parse(responseText);
        } catch (jsonError) {
          console.error('Bd parsowania JSON:', jsonError);
          console.error('Otrzymany tekst (pierwsze 500 znakw):', responseText.substring(0, 500));
          throw new Error('Otrzymano nieprawidowy format danych (nie JSON)');
        }
        
        // Sprawdzamy, czy otrzymane dane zawieraj informacj o bdzie
        if (data.error) {
          console.error('API zwrcio bd:', data.message, data);
          throw new Error(`Bd API: ${data.message}`);
        }
        
        // Aktualizujemy dane w aplikacji
        setAppData(data);
        console.log('Dane odwieone z API:', new Date().toLocaleTimeString());
      } catch (error) {
        console.error('Bd odwieania danych:', error);
        console.error('Szczegy bdu:', error.stack || error.toString());
        // Mona tutaj zaimplementowa pokazywanie bdu uytkownikowi, np. przez state
      }
    }
  }, [dataSource, dataLoaded]); // Dodaj zalenoci do useCallback
  
  // Automatyczne odwieanie danych
  useEffect(() => {
    let intervalId = null;
    
    if (autoRefresh && dataSource === 'api' && dataLoaded) {
      intervalId = setInterval(refreshData, refreshInterval);
      console.log(`Ustawiono automatyczne odwieanie co ${refreshInterval / 60000} minut`);
    }
    
    // Czyszczenie interwau przy odmontowaniu komponentu lub zmianie zalenoci
    return () => {
      if (intervalId) {
        clearInterval(intervalId);
        console.log('Wyczono automatyczne odwieanie');
      }
    };
  }, [autoRefresh, dataSource, dataLoaded, refreshInterval, refreshData]);

  return (
    <AppDataContext.Provider value={{
      appData,
      filters,
      dataLoaded,
      dataSource,
      autoRefresh,
      refreshInterval: refreshInterval / 60000, // Zwracamy w minutach dla atwiejszego uycia
      updateAppData,
      updateFilters,
      refreshData,
      toggleAutoRefresh,
      changeRefreshInterval
    }}>
      {children}
    </AppDataContext.Provider>
  );
};