// src/components/StatusBar/StatusBar.js
import React, { useState, useEffect } from 'react';
import { Alert, Button } from 'react-bootstrap';
import { useAppData } from '../../context/AppDataContext';
import './StatusBar.css';

function StatusBar() {
  const { dataSource, autoRefresh, refreshInterval, refreshData, appData } = useAppData();
  const [lastRefreshTime, setLastRefreshTime] = useState(null);
  const [isRefreshing, setIsRefreshing] = useState(false);
  const [nextRefreshTime, setNextRefreshTime] = useState(null);
  const [timeRemaining, setTimeRemaining] = useState(null);
  
  // Ustawiamy czas ostatniego odwieenia przy pierwszym renderowaniu
  // lub gdy zmieni si dane
  useEffect(() => {
    if (dataSource === 'api' && appData.meta && appData.meta.lastUpdate) {
      setLastRefreshTime(new Date().toLocaleTimeString());
    }
  }, [dataSource, appData]);
  
  // Ustawiamy czas nastpnego odwieenia
  useEffect(() => {
    if (dataSource === 'api' && autoRefresh) {
      const now = new Date();
      const next = new Date(now.getTime() + refreshInterval * 60 * 1000);
      setNextRefreshTime(next.toLocaleTimeString());
      
      // Aktualizujemy licznik odliczajcy czas do nastpnego odwieenia
      const intervalId = setInterval(() => {
        const now = new Date();
        const nextRefresh = new Date(now.getTime() + refreshInterval * 60 * 1000);
        const diffMs = nextRefresh - now;
        
        if (diffMs <= 0) {
          setTimeRemaining("Odwieanie...");
        } else {
          const diffMins = Math.floor(diffMs / 60000);
          const diffSecs = Math.floor((diffMs % 60000) / 1000);
          setTimeRemaining(`${diffMins}:${diffSecs < 10 ? '0' : ''}${diffSecs}`);
        }
      }, 1000);
      
      return () => clearInterval(intervalId);
    } else {
      setNextRefreshTime(null);
      setTimeRemaining(null);
    }
  }, [dataSource, autoRefresh, refreshInterval, lastRefreshTime]);
  
  // Funkcja do rcznego odwieenia danych
  const handleRefresh = async () => {
    setIsRefreshing(true);
    await refreshData();
    setLastRefreshTime(new Date().toLocaleTimeString());
    setIsRefreshing(false);
  };
  
  // Nie wywietlamy paska statusu, jeli dane nie pochodz z API
  if (dataSource !== 'api') {
    return null;
  }

  return (
    <Alert variant="info" className="status-bar d-flex justify-content-between align-items-center">
      <div>
        <strong>Status API:</strong> Poczono z Google Sheets
        {lastRefreshTime && <span> | Ostatnie odwieenie: {lastRefreshTime}</span>}
        {autoRefresh && nextRefreshTime && (
          <span> | Nastpne odwieenie: {timeRemaining ? `za ${timeRemaining}` : "wkrtce"}</span>
        )}
      </div>
      <Button 
        variant="outline-primary" 
        size="sm" 
        onClick={handleRefresh}
        disabled={isRefreshing}
      >
        {isRefreshing ? 'Odwieanie...' : 'Odwie teraz'}
      </Button>
    </Alert>
  );
}

export default StatusBar;